.octicon {
  display: inline-block;
  vertical-align: text-top;
  fill: currentColor;
}

.flash {
  position: relative;
  padding: 20px 16px;
  color: #24292e;
  border-style: solid;
  border-width: 1px;
  border-radius: 6px;
}
.flash p:last-child {
  margin-bottom: 0;
}
.flash .octicon {
  margin-right: 12px;
}

.flash-messages {
  margin-bottom: 24px;
}

.flash-close {
  float: right;
  padding: 16px;
  margin: -16px;
  text-align: center;
  cursor: pointer;
  background: none;
  border: 0;
  appearance: none;
}
.flash-close:hover {
  opacity: 0.7;
}
.flash-close:active {
  opacity: 0.5;
}
.flash-close .octicon {
  margin-right: 0;
}

.flash-action {
  float: right;
  margin-top: -3px;
  margin-left: 24px;
  background-clip: padding-box;
}

.flash {
  background-color: #dbedff;
  border-color: rgba(4, 66, 137, 0.2);
}
.flash .octicon {
  color: rgba(4, 66, 137, 0.6);
}

.flash-warn {
  background-color: #fffbdd;
  border-color: rgba(176, 136, 0, 0.2);
}
.flash-warn .octicon {
  color: #b08800;
}

.flash-error {
  background-color: #ffe3e6;
  border-color: rgba(158, 28, 35, 0.2);
}
.flash-error .octicon {
  color: rgba(158, 28, 35, 0.6);
}

.flash-success {
  background-color: #dcffe4;
  border-color: rgba(23, 111, 44, 0.2);
}
.flash-success .octicon {
  color: rgba(23, 111, 44, 0.8);
}

.flash-full {
  margin-top: -1px;
  border-width: 1px 0;
  border-radius: 0;
}

.flash-banner {
  position: fixed;
  top: 0;
  z-index: 90;
  width: 100%;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-radius: 0;
}

.warning {
  padding: 0.5em;
  margin-bottom: 0.8em;
  font-weight: 600;
  background-color: #fffbdd;
}

